@import '~node_modules/modern-normalize/modern-normalize.css';

body {

    font-family: $main-font;
    color: $main-font-color;
}

a:link {
    text-decoration: none;
}

a:visited {
    text-decoration: none;
    color: inherit;
}

a:hover {
    text-decoration: none;
}

a:active {
    text-decoration: none;
}

.container {
    max-width: 1080px;
    padding: 12px;
}